body{background: #E0E7E9;}
ul{padding: 0;}li{list-style: none;}
/* main ****************************/
.App{
  margin: 0 auto;
  max-width: 768px;
  padding: 1rem;
  background: white;
  border-radius: 10px;
}
/************** public**************/
.mt{
  font-size: 24px;
  padding: 1rem;
  color: #354649;
  font-weight: bold;
  box-sizing: border-box;
  border-bottom: 1px solid #354649;
}
/* Todolist ********/
.lt{
  font-size: 20px;
  font-weight: bold;
  color: #354649;
  padding: 1rem;
  border-bottom: 1px solid #354649;
}
.todoDelete{
  border : none;
  background: white;
  color: white;
  font-size: 1.2rem;
  margin-left: 1rem;
  cursor: pointer;
}
.addToggle {
  margin: 0 auto;
  cursor: pointer;
  border-radius: 50%;
  background: black;
  color: white;
  text-align: center;
  max-width: 50px;
  height: 50px;
  line-height: 40px;
  font-size: 50px;
  transform: rotate(0deg);
  transition: all 0.5s;

}
.rotate {
  transform: rotate(-45deg);
}